import EmptyData from '../../../components/empty-data.vue'
import ListItemOne from '../../../components/list-item-one.vue'
import ListItemTwo from '../../../components/list-item-two.vue'
import ListItemThree from '../../../components/list-item-three.vue'
import list from '../../../mixins/List.js'
export default {
	name: 'LostList',
	props: {
		show: {
			type: Boolean,
			default: false
		},
		url: {
			type: String,
			default: ''
		}
	},
	mixins: [list],
	components: {
		EmptyData,
		ListItemOne,
		ListItemTwo,
		ListItemThree
	},
	methods: {
		getData () {
			this.$http({
				url: this.url,
				loadingTip: null,
				params: {
					page: this.page.num
				},
				onRequestSuccess: (res) => {
					if (res.data) {
						res.data.forEach((item) => {
							item.commentInfo = Number(item.helps_num) === 0 ? '暂无回复' : item.helps_num + '爱心'
							if (item.images && typeof item.images === 'string') {
								item.images = item.images.split(',')
							}
							if (!item.images || item.images.length === 0) {
								item.type = 0
							} else if (item.images.length < 3) {
								item.type = 1
							} else {
								item.type = 2
							}
						})
					}
					this.loadSuccess(res.data)
				},
				onRequestFail: (errorCode, error) => {
					this.loadFail()
				}
			})
		},
		itemClick (item) {
			this.$push('/pages/lost/lost-info?lid=' + item.id)
		},
		scrolltolower () {
			this.onReachBottom()
		}
	},
	watch: {
		show (newVal, oldVal) {
			if (newVal) {
				if (this.list.length === 0) {
					this.getData()
				}
			}
		}
	},
	mounted() {
		if (this.show) {
			this.getData()
		}
	}
}
